/* Container and general styles */
.container {
    display: flex;
    flex-direction: column;
    margin-top: 226px;
    align-items: center;
}

/* h1 styles */
.h1 {
    position: absolute;
    top: 27px;
    left: 36px;
    color: #4053ff;
    text-align: center;
    font-size: 47.962px;
    font-weight: 800;
    line-height: normal;
    cursor: pointer;
}

/* h2 styles */
.h2 {
    color: #000;
    font-size: 30px;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 100px;
}

/* Instruction box styles */
.instructionbox {
    position: absolute;
    justify-self: start;
    margin-top: 100px;
}

/* span styles */
.span {
    color: #000;
    font-size: 25px;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 10px;
    align-self: start;
}

/* Input styles */
.input {
    display: flex;

    width: 440px;
    height: 90px;
    padding-left: 10px;
    justify-content: center;
    align-items: center;

    flex-shrink: 0;
    border-radius: 10px;
    border: 2px solid #4053ff;
    background: #fff;
    margin-bottom: 20px;
    font-size: 21px;
}

.icon {
    width: 100px;
    height: 100px;
    pointer-events: none;
}

.file {
    display: none;
}

.file::file-selector-button {
    font-size: 14px;
    background: #fff;
    border: 1px solid #111;
    border-radius: 12px;
    padding: 4px 32px;
    cursor: pointer;
}

.preview {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 20px auto;
    gap: 10px;
    width: 100%;
    padding: 70px;
    border: 3px dashed #eee;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
}

.preview:hover {
    border-color: #111;
}

.preview.active {
    background-color: #efeef3;
    border-color: #111;
}

.preview_msg {
    font-weight: 500;
    font-size: 18px;
    margin: 20px 0 10px;
}

.preview_desc {
    margin: 0;
    font-size: 14px;
}

/* Gender buttons container styles */
.genderButtons {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

/* Gender button styles */
.genderButton {
    padding: 10px 20px;
    width: 138px;
    height: 59px;
    flex-shrink: 0;
    border-radius: 10px;
    background: #d9d9d9;
    color: #000;
    text-align: center;
    font-size: 24px;
    font-weight: 600;
    border: none;
    line-height: normal;
    cursor: pointer;
}

.genderButton.selected {
    color: white;
    border-radius: 10px;
    background: #4053ff;
}

/* Form styles */
.signUp {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

/* Next button container styles */
.nextbox {
    position: absolute;
    top: 550px;
    left: 130%;
    transform: translateX(-50%);
    align-items: start;
    width: 110%;
    text-align: center;
    align-self: center;
    line-height: 70px;
    display: flex;
    justify-content: space-around;
}

/* Next button styles */
.nextBtn {
    width: 148px;
    height: 49.109px;
    flex-shrink: 0;
    border-radius: 15.337px;
    background: #4053ff;
    border: none;
    color: #fff;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
}

/* Back button container styles */
.back {
    margin-top: 3%;
    margin-left: -80%;
}

/* Back button styles */
.backBtn {
    all: unset;
    background: #f6f6f6;
    cursor: pointer;
    background: inherit;
    border: none;
    box-shadow: none;
    border-radius: 0;
    padding: 0;
    overflow: visible;
    cursor: pointer;
}

.backBtn:hover {
    background: #fff;
    color: inherit;
}

/* Profile image styles */
.imagePreview {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.profileImage {
    max-height: 200px;
    max-width: 200px;
}

.imageContainer {
    display: flex;
    align-items: center;
    justify-content: left;

    gap: 20px;
}

.imagetext {
    margin-top: 5%;
    font-size: 1.4rem;
}

.imageUpload button {
    padding: 10px 20px;
    background-color: #4053ff;
    color: white;
    font-size: 18px;
    font-weight: 600;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

.imageUpload button:hover {
    background-color: #3044cc;
}
/* Media queries for responsiveness */
@media (max-width: 1024px) {
    .h1 {
        font-size: 36px;
        top: 20px;
        left: 20px;
    }

    .h2 {
        font-size: 24px;
        margin-bottom: 80px;
    }

    .span {
        font-size: 20px;
    }

    .input {
        max-width: 350px;
        height: 60px;
        font-size: 18px;
    }

    .genderButton {
        width: 120px;
        height: 50px;
        font-size: 20px;
    }

    .nextbox {
        left: 250px;
        max-width: 500px;
    }

    .nextBtn {
        font-size: 20px;
        left: 100px;
    }
}

@media (max-width: 768px) {
    .container {
        margin-top: 150px;
    }

    .h1 {
        font-size: 30px;
        top: 15px;
        left: 15px;
    }

    .h2 {
        font-size: 20px;
        margin-bottom: 60px;
    }

    .span {
        font-size: 18px;
    }

    .input {
        max-width: 300px;
        height: 50px;
        font-size: 16px;
    }

    .genderButton {
        width: 100px;
        height: 40px;
        font-size: 18px;
    }

    .nextbox {
        left: 200px;
        max-width: 450px;
    }

    .nextBtn {
        font-size: 18px;
        left: 80px;
    }
}

@media (max-width: 480px) {
    .container {
        margin-top: 100px;
    }

    .h1 {
        font-size: 24px;
        top: 10px;
        left: 10px;
    }

    .h2 {
        font-size: 18px;
        margin-bottom: 50px;
    }

    .span {
        font-size: 16px;
    }

    .input {
        max-width: 250px;
        height: 40px;
        font-size: 14px;
    }

    .genderButton {
        width: 80px;
        height: 35px;
        font-size: 16px;
    }

    .nextbox {
        left: 100px;
        max-width: 400px;
    }

    .nextBtn {
        font-size: 16px;
        left: 50px;
    }
}
